import React, { useContext, useState, useEffect } from 'react';
import io from 'socket.io-client';
import useSound from 'use-sound';
import config from '../../../config';
import LatestMessagesContext, { LatestMessages } from '../../../contexts/LatestMessages/LatestMessages';
import TypingMessage from './TypingMessage';
import Header from './Header';
import Footer from './Footer';
import Message from './Message';
import '../styles/_messages.scss';
import initialBottyMessage from '../../../common/constants/initialBottyMessage';

const socket = io(
  config.BOT_SERVER_ENDPOINT,
  { transports: ['websocket', 'polling', 'flashsocket'] }
);

function Messages() {
  const [playSend] = useSound(config.SEND_AUDIO_URL);
  const [playReceive] = useSound(config.RECEIVE_AUDIO_URL);
  const { setLatestMessage } = useContext(LatestMessagesContext);

  const [messages, setMessages] = useState([{message: initialBottyMessage, user: "bot"}]);
  const [message, setMessage] = useState({message: initialBottyMessage, user: "bot"});
  const [showTyping, setShowTyping] = useState(false);

  socket.on('bot-message', (message) => {
    setLatestMessage(message);
    playReceive();
  });
  
  socket.on('bot-typing', () => {
    setShowTyping(true);
    setTimeout(() => {
      setShowTyping(false);
    }, 500);
  });

  useEffect(() => {

  }, [])
  
  
  useEffect(() => {
    setLatestMessage(message);
  }, [playSend, playReceive]);

  const sendMessage = (message) => {
    if (message) {
      socket.emit('user-message', message);
      playSend();
    }
  }

  const onChangeMessage = (event) => {
    setMessage(event.target.value);
  }

  function showTypingMessage() {
    if(showTyping) {
      return TypingMessage();
    }
  }

  function showMessages() {
    return(
      <ul>
        {
          messages.map((item) => (
            <Message message={item} nextMessage={""} botTyping={false} />
          ))
        }  
        </ul>
    );
  }

  return (
    <div className="messages">
      <Header />
      <div className="messages__list" id="message-list">
        <showMessages />
      </div>
      <Footer message={message} sendMessage={sendMessage} onChangeMessage={onChangeMessage} />
    </div>
  );
}

export default Messages;
